import React from "react";
import { connect } from "react-redux";
import { Redirect } from "react-router";
import { Link } from "react-router-dom";
import {
  Auth,
  FC_AddBasicInfo,
  FC_Login,
  FT_AddBasicInfo,
  FT_Login,
} from "../../actions";
import Alert from "../../components/Alert/Alert";
import Button from "../../components/Buttons/Button";
import TextInput from "../../components/Inputs/TextInput";
import { StoreState } from "../../reducers";
import LoginCheckAccountMessage from "./LoginCheckAccountMessage";

// props for the component
interface AppProps {
  auth: Auth;
  FC_Login: FT_Login;
  FC_AddBasicInfo: FT_AddBasicInfo;
}

interface AppState {
  username: string;
  password: string;
  loading: boolean;
  no_password: boolean;
  error: {
    target: "username" | "password" | "main" | "SEND-CODE-ERROR" | null;
    msg: string;
  };
  checking_account: boolean;
}

class _App extends React.Component<AppProps, AppState> {
  constructor(props: AppProps) {
    super(props);

    this.state = {
      checking_account: false,
      no_password: false,
      username: "",
      password: "",
      loading: false,
      error: {
        target: null,
        msg: "",
      },
    };
  }

  submitLogin = (e: any) => {
    e.preventDefault();

    //* Validation
    if (this.state.username.length <= 4) {
      return this.setState({
        error: {
          target: "username",
          msg: "Phone must not be lass than 4 character",
        },
      });
    }

    //* Validation
    if (this.state.password.length <= 4 && this.state.no_password === false) {
      return this.setState({
        error: {
          target: "password",
          msg: "Password must not be lass than 4 character",
        },
      });
    }

    this.setState({
      loading: true,
      error: {
        target: null,
        msg: "",
      },
    });

    this.props.FC_Login(
      {
        password: this.state.password,
        username: this.state.username,
      },
      (auth_status, data, msg) => {
        console.log({ auth_status, msg });
        if (auth_status === true) {
          // check for error (api)

          if (data?.is_account_verified === true) {
            this.props.FC_AddBasicInfo((info_status, msg) => {
              // if (info_status === true) {
              //   // this.props.FC_AddBasicInfo((status, msg) => {});
              // }
            });
          } else {
            this.setState({
              loading: false,
              checking_account: true,
              error: {
                msg: msg,
                target: "SEND-CODE-ERROR",
              },
            });
          }
        } else {
          this.setState({
            error: {
              target: "main",
              msg: msg,
            },
            loading: false,
          });
        }
      }
    );
  };

  render() {
    if (this.props.auth.isAuthenticated === true) {
      return <Redirect to="/dashboard" />;
    }

    if (this.state.checking_account === true)
      return (
        <LoginCheckAccountMessage
          close={() => this.setState({ checking_account: false, password: "" })}
          phone={this.state.username}
          msg={
            this.state.error.target === "SEND-CODE-ERROR"
              ? this.state.error.msg
              : ""
          }
        />
      );

    return (
      <>
        <div className="min-h-screen bg-gray-100 dark:bg-dark-900 flex items-center ">
          <div className="bg-white py-10 rounded border mt-5  dark:bg-dark-800 w-full max-w-md mx-auto px-5 animate__animated animate__zoomIn animate__faster">
            {this.state.error.target === "main" && (
              <div className="text-center bg-red-100 p-0.5 px-2 pb-2 mb-2 rounded animate__animated animate__zoomIn animate__faster">
                <Alert theme="danger">{this.state.error.msg}</Alert>

                <Link
                  to="/forget-password"
                  className="hover:underline hover:bold cursor-pointer"
                >
                  Click here to reset your password
                </Link>
              </div>
            )}

            <form onSubmit={this.submitLogin}>
              <h1 className="text-center text-3xl mb-6 text-primary-800 dark:text-primary-dark  font-extrabold">
                TMIS Login
              </h1>

              {this.state.no_password === true && (
                <div className="text-center text-gray-500">
                  Finding your account
                </div>
              )}

              <TextInput
                error={
                  this.state.error.target === "username"
                    ? this.state.error.msg
                    : ""
                }
                label="Phone"
                name="phone"
                onChange={(e) =>
                  this.setState({
                    username: e.target.value,
                    error: {
                      target: null,
                      msg: "",
                    },
                  })
                }
                placeholder="25070000000"
                value={this.state.username}
                disabled={this.state.loading}
                type="number"
              />

              {this.state.no_password === false && (
                <TextInput
                  error={
                    this.state.error.target === "password"
                      ? this.state.error.msg
                      : ""
                  }
                  label="Password"
                  name="password"
                  onChange={(e) =>
                    this.setState({
                      password: e.target.value,
                      error: {
                        target: null,
                        msg: "",
                      },
                    })
                  }
                  placeholder="Password"
                  value={this.state.password}
                  disabled={this.state.loading}
                  type="password"
                />
              )}

              <div className="mt-5">
                <Button full loading={this.state.loading}>
                  {this.state.no_password === true ? "Continue" : "Login"}
                </Button>
              </div>

              {/* <div className="mt-4 text-center">
                <button
                  type="button"
                  className="font-bold hover:text-blue-600 cursor-pointer"
                  onClick={() =>
                    this.setState({
                      no_password: !this.state.no_password,
                    })
                  }
                >
                  {this.state.no_password === true
                    ? "I have a password"
                    : "I don't have a password."}
                </button>
              </div> */}

              <div className="mt-4 text-center">
                <Link to="/forget-password" className=" cursor-pointer">
                  Forget your password?
                </Link>
              </div>
            </form>
          </div>
        </div>
      </>
    );
  }
}

const mapStateToProps = ({ auth }: StoreState): { auth: Auth } => {
  return { auth: auth };
};

export const Login = connect(mapStateToProps, {
  FC_Login,
  FC_AddBasicInfo,
})(_App);
